.map__view {
  background-color: #e8e0d5;
  border-radius: 0.6rem;
  border: 1px solid #5a5a5a;
  min-height: 29rem;
  height: calc(100vh - 25rem);
  margin: 1.6rem 0 0 0;
  text-align: center;
  position: relative;
}
.map__view__header {
  background-color: #5a5a5a;
  border-radius: 0.5rem 0.5rem 0 0;
  height: 3.75rem;
}

.gm-style .gm-style-iw {
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: auto !important;
  min-width: 8rem;
  min-height: 9rem !important;
  height: 9rem !important;
  padding: 0;
}
.gm-style .gm-style-iw > div:first-of-type {
  margin: 0;
  width: 100%;
  max-width: none !important;
  max-width: initial !important;
}

.cart__column {
  border-top-right-radius: 0.8rem;
  border-bottom-right-radius: 0.8rem;
  background: white;
}
.cart__column__header {
  color: white;
  background-color: #1d1d1d;
  border-top-right-radius: 0.8rem;
  height: 3.4rem;
  white-space: nowrap;
}
.cart__column__header__icon:before,
.cart__column__header__icon:after {
  font-size: 2.3rem;
  margin: 0 0 0 1rem;
  vertical-align: middle;
}
.cart__column__header h4 {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.1rem;
  margin: 0.2rem 0 0 1.2rem;
  font-weight: 200;
}
.cart__column__cart {
  min-height: 11rem;
  text-align: center;
  color: white;
}
.shop__overview__column {
  margin-top: 1.4rem;
}

.order__form__inner {
  padding: 1rem;
}

